@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  position: relative;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/
body {
  background: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 1.8rem;
  line-height: 2.2rem;
  font-weight: 400;
  color: #000;
}

.lock {
  overflow: hidden;
}

.page {
  position: relative;
}

.up {
  position: fixed;
  bottom: 1rem;
  right: 4rem;
  max-width: 2.5rem;
  z-index: 1;
}
.up__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  opacity: 0.4;
  transition: all 0.3s;
}
.up__link:hover {
  opacity: 1;
}
.up__link:hover .up__link-text {
  visibility: visible;
}
.up__link-text {
  visibility: hidden;
  font-size: 1.4rem;
  color: #ED7163;
}

.wrapper {
  background: #F6F6F6;
}

.inner {
  padding: 12rem 0;
}

a {
  color: #828282;
}

.container {
  max-width: 94rem;
  padding: 0 1rem;
  margin: 0 auto;
  text-align: center;
}

.btn {
  display: inline-block;
  background: #828282;
  line-height: 4rem;
  color: #F6F6F6;
  text-align: center;
  transition: color 0.3s;
}
.btn:hover {
  color: #000;
}

.title {
  font-size: 3.4rem;
  line-height: 4.2rem;
  font-weight: 500;
  color: #000;
  line-height: 0.2rem;
  display: inline-block;
  padding: 2rem 2rem 0 2rem;
  background-color: #ea5241;
}

.header__inner {
  padding: 3rem 0 4rem;
  border-bottom: 1px solid #828282;
}

.menu__btn {
  display: none;
}
.menu__list {
  display: flex;
  justify-content: space-between;
}
.menu__list-link {
  transition: color 0.3s;
}
.menu__list-link:hover {
  color: #000;
}

.hero__inner {
  padding: 4rem 0 10rem;
  position: relative;
}
.hero__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
}
.hero__top a {
  color: #828282;
  transition: color 0.3s;
}
.hero__top a:hover {
  color: #000;
}
.hero__title {
  max-width: 10rem;
  font-size: 4rem;
  line-height: 4.4rem;
  font-weight: 900;
  color: #000;
}
.hero__text {
  max-width: 18.5rem;
}
.hero__lang {
  vertical-align: middle;
  transform: rotate(-90deg) translate(50%, -100%);
  transform-origin: right top;
}
.hero__lang-mobile {
  padding: 0.5rem 0 0.5rem;
  position: absolute;
  right: 1rem;
  bottom: 0;
}
.hero__menu-mobile {
  display: none;
}
.hero__menu-item {
  width: 100%;
}
.hero__photo {
  width: 100%;
  min-height: 39rem;
  background: url(../../assets/img/hero-photo.jpg) no-repeat;
  background-position: center;
  background-size: cover;
  filter: grayscale(0.8);
}
.hero__menu-list {
  display: none;
}

.hero__menu-btn {
  display: none;
  position: relative;
  width: 3.5rem;
  height: 2.4rem;
}

.about__title {
  margin-bottom: 2.5rem;
}
.about__items {
  margin: 0 auto;
  max-width: 60rem;
}
.about__items p {
  margin-bottom: 2rem;
}
.about__items a {
  transition: color 0.3s;
}
.about__items a:hover {
  color: #000;
}

.skills__items {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-flow: row;
  gap: 2rem;
}
.skills__title {
  margin-bottom: 7rem;
}
.skills__item {
  max-width: 17rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  margin-bottom: 2rem;
}
.skills__item--img {
  max-width: 8rem;
  max-height: 8rem;
  margin-bottom: 2.5rem;
}
.skills__item--title {
  font-size: 1.6rem;
  line-height: 1.6rem;
  font-weight: 400;
  color: #828282;
}
.skills__item--text {
  max-width: 100%;
  word-wrap: break-word;
}

.portfolio__title {
  margin-bottom: 4.7rem;
}
.portfolio__item {
  margin-bottom: 10rem;
  box-shadow: 0 0.3rem 1rem rgba(192, 192, 192, 0.1);
}
.portfolio__item:last-child {
  margin-bottom: 0;
}
.portfolio__link {
  position: relative;
  display: block;
  margin-bottom: 2.5rem;
  max-width: 100%;
  max-height: 52rem;
}
.portfolio__link.dev:after {
  position: absolute;
  content: "in work";
  top: 0;
  left: 0;
  font-size: 2rem;
  font-weight: 500;
  padding: 1rem 3rem;
  color: #f1f1f1;
  background: #ea5241;
  text-shadow: -1px -1px 1px rgba(255, 255, 255, 0.1), 1px 1px 1px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
}
.portfolio__text--link {
  display: block;
  margin-bottom: 0.5rem;
  text-decoration: underline;
  transition: color 0.3s;
}
.portfolio__text--link:hover {
  color: #000;
}
.portfolio__description {
  padding-bottom: 1rem;
  font-size: 1.5rem;
}
.portfolio__description a {
  transition: color 0.3s;
}
.portfolio__description a:hover {
  color: #000;
}

.certificates__title {
  margin-bottom: 7rem;
}
.certificates__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
.certificates .certificates__link {
  box-shadow: 0 0.3rem 1rem rgba(192, 192, 192, 0.2);
}

.footer__title {
  margin-bottom: 3rem;
}
.footer__text {
  margin-bottom: 3rem;
}
.footer__mail {
  display: inline-block;
  font-size: 1.8rem;
  line-height: 2rem;
  font-weight: 700;
  color: #fff;
  background: #000;
  padding: 1.2rem 3rem;
  margin-bottom: 9rem;
  border-radius: 3rem;
  transition: background 0.3s;
}
.footer__mail:hover {
  background: #ea5241;
  color: #000;
}
.footer__bottom {
  color: #828282;
}

.social {
  display: flex;
  justify-content: center;
  margin-bottom: 5rem;
}
.social__link {
  display: block;
  width: 5rem;
  border: 0.2rem solid #000;
  padding: 1rem;
  border-radius: 1rem;
  transition: transform 0.3s;
}
.social__link:hover {
  transform: scale(1.2);
}
.social__link + .social__link {
  margin-left: 6rem;
}

@media (max-width: 768px) {
  .inner {
    padding: 5rem 0;
  }

  .up {
    display: none;
  }

  .header {
    display: none;
  }
  .header .header__lang {
    display: none;
  }

  .hero__inner {
    padding: 1rem 0 10rem;
  }
  .hero__lang {
    display: none;
  }
  .hero__menu-mobile {
    display: block;
  }
  .hero__photo {
    width: 100%;
    min-height: 39rem;
    background: url(../../assets/img/hero-photo--mobile.jpg) no-repeat;
    background: url(../../assets/img/hero-photo.jpg) no-repeat;
    background-position: 60% 40%;
    background-position: right 25% center;
    background-size: cover;
  }

  .hero__menu {
    order: 1;
  }
  .hero__menu-btn {
    display: block;
    background: transparent;
    position: fixed;
    top: 2rem;
    right: 1rem;
    z-index: 2;
  }
  .hero__menu-btn--active {
    opacity: 0.3;
  }
  .hero__menu-btn-line {
    display: block;
    width: 100%;
    height: 0.2rem;
    background: black;
    position: absolute;
    left: 0;
    transition: all 0.3s linear;
  }
  .hero__menu-btn-line--first {
    top: 0;
  }
  .hero__menu-btn-line--second, .hero__menu-btn-line--third {
    top: 50%;
    transform: translateY(-50%);
  }
  .hero__menu-btn-line--fourth {
    bottom: 0;
    color: black;
  }
  .hero__menu-btn--active .hero__menu-btn-line--first, .hero__menu-btn--active .hero__menu-btn-line--fourth {
    opacity: 0;
  }
  .hero__menu-btn--active .hero__menu-btn-line--second {
    transform: translateY(-50%) rotate(45deg);
  }
  .hero__menu-btn--active .hero__menu-btn-line--third {
    transform: translateY(-50%) rotate(-45deg);
  }
  .hero__menu-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: fixed;
    text-align: center;
    opacity: 0.95;
    top: 0;
    left: 0;
    right: 0;
    padding-bottom: 2rem;
    background-color: #ffffff;
    transform: translatex(80rem);
    transition: all 0.5s;
  }
  .hero__menu-list.hero__menu-list--active {
    z-index: 1;
    transform: translateY(0);
  }
  .hero__menu-list-link {
    display: block;
    font-size: 1.8rem;
    line-height: 2.8rem;
    padding: 2rem 0;
  }

  .certificates__items {
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (max-width: 576px) {
  .hero__title {
    font-size: 3rem;
    line-height: 3.4rem;
  }

  .skills__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10rem;
  }
}
@media (max-width: 480px) {
  .hero__top {
    display: block;
  }
  .hero__title {
    margin-bottom: 1rem;
  }
  .hero__text {
    text-align: left;
  }
  .about__items p {
    font-size: 1.6rem;
  }

  .certificates__items {
    width: 90%;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    margin: 0 auto;
  }

  .social__link:hover {
    transform: none;
  }
  .footer__bottom {
    font-size: 1.6rem;
  }
}
@media (max-width: 320px) {
  .hero__top {
    flex-wrap: wrap;
  }
  .hero__title {
    margin-bottom: 1rem;
  }
  .hero__menu-mobile {
    order: 2;
  }
  .hero__photo {
    background-size: 500%;
  }

  .about__items p {
    font-size: 1.4rem;
  }

  .certificates__items {
    width: 100%;
  }
}